(() => {
    const PREFIX = "experiment_";
    const AVAILABLE_EXPERIMENTS = [];
    const experiments = {};

    function start() {
        AVAILABLE_EXPERIMENTS.forEach((experiment) => {
            try {
                const key = PREFIX + experiment.name;

                const variantName =
                    localStorage.getItem(key) ||
                    experiment.variants[Math.trunc(Math.random() * experiment.variants.length)]
                        .name;

                localStorage.setItem(key, variantName);

                experiments[experiment.name] = experiment.variants.find(
                    ({ name }) => name === variantName
                );
            } catch (_) {
                return;
            }

            gtag("event", `${key}_${variantName}`);

            gtag("event", "experiment", {
                name: key,
                variant: variantName
            });
        });
    }

    window.experimentValueFor = function experimentValueFor(experimentName, key) {
        return experiments[experimentName].data[key];
    };

    start();
})();
