(() => {
    function displayFeedback(e) {
        e.parentElement.classList.add("newsroom-internal-feedback-visible");

        setTimeout(() => {
            e.parentElement.classList.remove(
                "newsroom-internal-feedback-visible"
            );
        }, 3_000);
    }

    globalThis.Newsroom = {
        copyText(e) {
            e.preventDefault();

            const title = document.querySelector("h1").innerText;

            const body = Array.from(
                document.querySelectorAll(
                    "time, .newsroom-internal-body p, .newsroom-internal-body blockquote, .newsroom-internal-body li"
                )
            )
                .map((e) => {
                    if (e.nodeName === "LI") {
                        return `\n- ${e.innerText}\n`;
                    }

                    return `\n${e.innerText}\n`;
                })
                .join("");

            navigator.clipboard.writeText(`${title}\n${body}`);

            displayFeedback(e.target);
        },

        downloadImages(e) {
            e.preventDefault();

            document
                .querySelectorAll(".newsroom img[data-downloadable]")
                .forEach((image) => {
                    const a = document.createElement("a");
                    a.href = image.src;
                    a.target = "_blank";
                    a.download = image.src.split("/").reverse()[0];
                    a.click();
                });
        },

        copyLink(e) {
            navigator.clipboard.writeText(location.href);

            displayFeedback(e.target);
        }
    };
})();
